/* eslint-disable import/no-anonymous-default-export */
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Card,
  CardContent,
  Typography,
  CardActions,
  Button,
} from "@material-ui/core";
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import ReactHtmlParser from "react-html-parser";
import "../CSS/FloatingCard.css";
import { FloatingCardProps } from "../interfaces";

const styles = makeStyles({
  root: {
    padding: "0.5em",
  },
  secondaryTitle: {
    marginBottom: 12,
    minHeight: "1.5em",
  },
  body: {
    textAlign: "justify",
  },
  button: {
    textAlign: "right",
    width: "100%",
  },
});

export default (props:FloatingCardProps) => {
  const classes = styles();
  const {jsonData, func} = props;
  //const { title, secondaryTitle, discription } = props.JsonData;
  //const { next, isNextEnabled, previous, isPreviousEnabled } = props.func;

  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography variant="h4" component="h2">
          {jsonData.title}
        </Typography>
        <Typography className={classes.secondaryTitle} color="textSecondary">
          {jsonData.secondaryTitle}
        </Typography>
        <Typography className={classes.body} variant="body2" component="p">
          {ReactHtmlParser(jsonData.discription)}
        </Typography>
      </CardContent>
      <CardActions>
        <div className={classes.button}>
          <Button
            style={{ marginRight: "1em" }}
            variant="contained"
            color="secondary"
            disabled={!func.isPreviousEnabled}
            startIcon={
              <NavigateNextIcon style={{ transform: "rotate(180deg)" }} />
            }
            onClick={() => func.previous()}
          >
            Previous
          </Button>
          <Button
            variant="contained"
            color="primary"
            disabled={!func.isNextEnabled}
            endIcon={<NavigateNextIcon />}
            onClick={() => func.next()}
          >
            Next
          </Button>
        </div>
      </CardActions>
    </Card>
  );
};
