import React from "react";
import { Grid } from "@material-ui/core";
import { Background, Body } from "./Layouts";
import { FloatingCard, ExtraCard } from "./Components";
import { JsonElement } from "./interfaces";
//import JsonData from "./Assets/data.json";

export default class App extends React.Component {
  state = { index: 0 };
  JsonData:JsonElement[] = require("./Assets/data.json");

  constructor(props:any) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }

  isEmptyObj(obj:any): boolean {
    return !Object.keys(obj).length;
  }

  next() {
    const { index } = this.state;

    if (index + 1 >= this.JsonData.length)
      this.setState({ index: this.JsonData.length - 1 });
    else
      this.setState({ index: index + 1 });
  }

  previous() {
    const { index } = this.state;

    if (index - 1 <= 0)
      this.setState({ index: 0 });
    else
      this.setState({ index: index - 1 });
  }

  render() {
    return (
      <React.Fragment>
        <Background />
        <Body>
          <Grid item xs={3} style={{ zIndex: -1 }}>
            <ExtraCard
              data={this.JsonData[this.state.index].extraCard}
              isEnabled={!this.isEmptyObj(this.JsonData[this.state.index].extraCard)}
            />
          </Grid>
          <Grid item xs={4}>
            <FloatingCard
              jsonData={this.JsonData[this.state.index]}
              func={{
                next: this.next,
                isNextEnabled: this.state.index < this.JsonData.length - 1,
                previous: this.previous,
                isPreviousEnabled: this.state.index > 0,
              }}
            />
          </Grid>
        </Body>
      </React.Fragment>
    );
  }
}
